import { Router } from '@angular/router';
import { AngularFireAuth } from '@angular/fire/auth';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-asso-login',
  templateUrl: './asso-login.page.html',
  styleUrls: ['./asso-login.page.scss'],
})
export class AssoLoginPage implements OnInit {
  dataUser ={
    email:'',
    password:''
  };

  constructor(public afAuth: AngularFireAuth,public router: Router) { }

  ngOnInit() {
  }

  login(){
    console.log('email:'+ this.dataUser.email);
    console.log('password:'+ this.dataUser.password);
    this.afAuth.signInWithEmailAndPassword(this.dataUser.email, this.dataUser.password) 
    .then(success => {
      this.router.navigate(['/tabs/tab1']);

    }, function (error) {
      console.log('Email ou mot de passe mal formaté');
    }
    );
    
  }

}
